.dogCards{
  width: 80%;
  justify-content: center;
  text-align: center;
  margin: auto;
}
.dogsGrid{
    display: grid;
    grid-template-columns: repeat(auto-fill, 300px);
    gap: 25px;
    
    justify-content: center;
  
}

@media (max-width: 560px){
    .dogsGrid {  grid-template-columns: 80%;
      }
  }

    .select {
      display: block;
      font-size: 14px;
      font-family: 'Verdana', sans-serif;
      font-weight: bold;
      color: #444;
      line-height: 1.3;
      padding: .4em 1.4em .3em .8em;
      width: 180px;
      max-width: 100%; 
      box-sizing: border-box;
      margin: 5px;
      margin-top: 10px;
      border: 1px solid #aaa;
      box-shadow: 0 1px 0 1px rgba(0,0,0,.03);
      border-radius: .3em;
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      background-color: rgb(255, 255, 255);
      background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
        linear-gradient(to bottom, #ffffff 0%,#f7f7f7 100%);
      background-repeat: no-repeat, repeat;
      background-position: right .7em top 50%, 0 0;
      background-size: .65em auto, 100%;
      cursor:url('../cursor_perrito_.png'), auto;
    }
    .select::-ms-expand {
      display: none;
    }
    .select:hover {
      border-color: #888;
    }
    .select:focus {
      border-color: #aaa;
      box-shadow: 0 0 1px 3px #aa926c;
      box-shadow: 0 0 0 3px -moz-mac-focusring;
      color: #222; 
      outline: none;
    }
    .select option {
      font-weight:normal;
    }
    
.inputs{
  display: flex;
    flex-flow: row;
    align-items: center;
  justify-content: center;
}

.navlink{
  text-decoration: none;
  outline:none;
  color: #222;
  
}

.margin{
  background: gainsboro;
  margin-top: 20px;
  border-radius: 15px;
  box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 10px #e60073,
  0 0 10px #e60073, 0 0 10px #e60073, 0 0 10px #e60073;
  cursor:url('../cursor_perrito.png'), auto;
  width: 250px;
}
.margin:hover {
  transform: scale(1.1);
  cursor:url('../cursor_perrito.png'), auto;
}
