.paginado {
  display: grid;
  grid-template-columns: repeat(auto-fill, 40px);
  gap: 5px;
  padding: 20px;
  justify-content: center;
  margin: 0px;
  cursor: url("../cursor_perrito_.png"), auto;
}

.number {
  color: black;
  list-style: none;
  display: flex;
  flex-grow: initial;
  font-size: 22px;
  font-style: italic;
  font-weight: bold;
  margin: auto;
  padding: 5px;
}

.number:hover{
  transform: scale(1.3);
}

.img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 30px;
  height: 30px;
  border-radius: 10px;
  position: relative;
}

.img::before {
  content: "";
    background: burlywood;
  background-image: url("../huellita.jpg");
  background-size: 30px 30px;
position: absolute;
top: 0; left: 0; bottom: 0; right: 0;
border-radius: 10px;
z-index: -1;
opacity: 0.9;
}

.nav {
  width: 70%;
  margin: auto;
}
